/* src/app/app/components/home/home.component.css */
.sidenav-container {
  height: 100vh;
}
.sidenav {
  width: 240px;
  background: #f4f4f4;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.logout-section {
  margin-top: auto;
  padding: 10px;
  text-align: center;
}
.logout-btn {
  color: #d32f2f;
  font-weight: bold;
}
.top-toolbar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.toolbar-title {
  font-size: 1.6rem;
  font-weight: bold;
}
.top-toolbar button {
  position: absolute;
  left: 10px;
}
.hero {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(https://images.unsplash.com/photo-1495435229349-e86db7bfa013?fm=jpg&q=60&w=3000) center/cover no-repeat;
  color: white;
  text-align: center;
  padding: 120px 20px;
}
.hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  animation: fadeInUp 1s ease;
}
.bulletin p {
  font-size: 1.2rem;
  margin: 8px 0;
  animation: fadeInUp 1.2s ease;
}
.guidelines {
  margin: 40px auto;
  max-width: 700px;
  background: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}
.guidelines h2 {
  margin-bottom: 16px;
  color: #1e88e5;
}
.guidelines ul {
  list-style: disc inside;
}
.guidelines li {
  margin: 8px 0;
  font-size: 1.1rem;
}
.footer {
  margin-top: 40px;
  background: #f8f8f8;
  padding: 30px 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #ddd;
}
.address {
  max-width: 60%;
}
.social-icons a {
  margin: 0 10px;
  font-size: 28px;
  color: #1e88e5;
  transition: 0.3s;
}
.social-icons a:hover {
  color: #1565c0;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
mat-sidenav-container {
  height: 100vh;
}
mat-sidenav {
  min-height: 100vh;
  overflow-y: auto;
}
.material-symbols-outlined {
  font-variation-settings:
    "FILL" 0,
    "wght" 400,
    "GRAD" 0,
    "opsz" 24;
  font-size: 24px;
}
/*# sourceMappingURL=home.component.css.map */
